<template>
  <div class="wrapper" :class="{ 'reversed': reversed }">
    <router-link to="/">
      <div class="content">
        <div class="header transition_1500">
          <svg-icon name="chevron-right" class="pt-1"/>
          <h3 class="header--content">
            {{ title }}
          </h3>
        </div>
        <img :src="img" alt="">
        <div class="overlay transition_1500"></div>
      </div>
    </router-link>
  </div>
</template>

<script>
import SvgIcon from '@/components/ui/SvgIcon'
export default {
  name: 'ImageTextHover',
  components: {
    SvgIcon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    img: {
      type: String,
      default: ''
    },
    reversed: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  position: relative;
  width: 100%;
  padding-top: 40px;
  .header {
    position: absolute;
    width: 100%;
    top: -20px;
    left: 0;
    display: flex;
    opacity: 0;
    &--content {
      display: inline-block;
      padding-left: 10px;
    }
  }
  .content {
    position: relative;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    &:hover {
      .header {
        transform: translateY(-20px);
        opacity: 1;
      }
      .overlay {
        transform: scale(1,0);
      }
    }
    .overlay {
      background: rgba(0,0,0,0.3);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      will-change: transform;
      transform-origin: top;
    }
  }
  &.reversed {
    padding-top: 0;
    padding-bottom: 40px;
    .header {
      top: auto;
      bottom: -20px;
    }
    .content {
      &:hover {
        .header {
          transform: translateY(20px);
        }
      }
    }
  }
}
</style>
