import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/jardin',
    name: 'Jardin',
    component: () => import('@/views/Jardin.vue')
  },
  {
    path: '/salon',
    name: 'Salon',
    component: () => import('@/views/Salon.vue')
  },
  {
    path: '/servicios',
    name: 'Servicios',
    component: () => import('@/views/Servicios.vue')
  },
  {
    path: '/galeria',
    name: 'Galeria',
    component: () => import('@/views/Galeria.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})

export default router
