<template>
  <div class="menu transition_1000" :class="{ 'hidden': !menu }">
    <div class="close" @click="close">
      <div class="close__x">
        <div class="line_1 transition_1500"></div>
        <div class="line_2 transition_1500"></div>
      </div>
      <p class="close__text transition_1500">cerrar</p>
    </div>
    <div class="logo">
      <img src="@/assets/img/icono.png" alt="">
    </div>
    <div class="content">
      <v-row class="align-center">
        <v-col cols="auto">
          <div class="book">
           <div class="book__btn">
              <div class="overflow-h">
                <p class="transition_1500 book__text to_top">RESERVA</p>
              </div>
              <div class="transition_1500 delayed_500 book__divider"></div>
              <div class="overflow-h">
                <p class="transition_1500 book__text to_bottom book__text--spaced">AHORA</p>
              </div>
            </div>
          </div>
        </v-col>
        <v-col>
          <v-row class="links">
            <v-col class="links__wrapper links__wrapper--first">
              <div class="overflow-h">
                <router-link class="transition_1500 link_item" to="/" @click.native="close">
                  INICIO
                  <div class="underline transition_1000"></div>
                </router-link>
              </div>
              <div class="overflow-h">
                <router-link class="transition_1500 link_item" to="/salon" @click.native="close">
                  SALÓN
                  <div class="underline transition_1000"></div>
                </router-link>
              </div>
              <div class="overflow-h">
                <router-link class="transition_1500 link_item" to="/jardin" @click.native="close">
                    JARDÍN
                    <div class="underline transition_1000"></div>
                </router-link>
              </div>
            </v-col>
            <div class="divider_vertical transition_1500"></div>
            <v-col class="links__wrapper links__wrapper--second">
              <div class="overflow-h">
                <router-link class="transition_1500 link_item" to="/servicios" @click.native="close">
                  SERVICIOS
                  <div class="underline transition_1000"></div>
                </router-link>
              </div>
              <div class="overflow-h">
                <router-link class="transition_1500 link_item" to="/galeria" @click.native="close">
                  GALERÍA
                  <div class="underline transition_1000"></div>
                </router-link>
              </div>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="auto" >
          <p class=" transition_1500 privacy_policy">
            aviso de <br>
            privacidad
          </p>
          <div class=" transition_1500 social">
            <svg-icons class="mr-2" name="facebook" color="#646368"/>
            <svg-icons name="instagram" color="#646368"/>
          </div>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import SvgIcons from '@/components/ui/SvgIcon'

export default {
  name: 'Menu',
  components: {
    SvgIcons
  },
  computed: {
    ...mapState(['menu'])
  },
  methods: {
    close () {
      this.$store.commit('SET_MENU', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.menu {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #FFF;
  display: flex;
  align-items: center;
  z-index: 100;
  &.hidden {
    opacity: 0;
    pointer-events: none;
    -webkit-transition: -webkit-transform 100000ms cubic-bezier(0.190, 1.000, 0.220, 1.000),opacity 800ms cubic-bezier(0.190, 1.000, 0.220, 1.000);
    transition: transform 100000ms cubic-bezier(0.190, 1.000, 0.220, 1.000),opacity 800ms cubic-bezier(0.190, 1.000, 0.220, 1.000);
  }
}
.close {
  position: absolute;
  top: 30px;
  right: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
  &__x {
    position: relative;
    width: 25px;
    height: 25px;
    transform: rotate(45deg);
    .line_1 {
      width: 25px;
      height: 3px;
      background: $font-color;
      position: absolute;
      top: 11px;
    }
    .line_2 {
      height: 25px;
      width: 3px;
      background: $font-color;
      position: absolute;
      top: 0px;
      left: 11px;
    }
  }
  &__text {
    color: $font-color;
    font-weight: 600;
    font-size: 16px;
    letter-spacing: 3.5px;
    // width: 46px;
    margin-top: 5px;
  }
}
.logo {
  position: absolute;
  width: 100px;
  top: 30px;
  left: 50%;
  margin-left: -50px;
  img {
    width: 100%;
  }
}
.content {
  width: 100%;
}
.book {
  position: relative;
  top: 0;
  width: 93px;
  // background: $primary;
  height: 95px;
  margin-left: 29px;
  margin-right: 29px;
  pointer-events: all;
  cursor: pointer;
  &__text {
    margin: 0;
    // color: #FFF;
    letter-spacing: 3px;
    &--spaced {
      letter-spacing: 6.2px;
    }
  }
  &__btn {
    p {
      text-align: center;
    }
  }
  &__divider {
    height: 5px;
    background: #212121;
    margin: 3px 0 3px 5px;
    width: 78px;
  }
}
.link_item {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  // border: 1px solid #111;
  padding-right: 30px;
  text-decoration: none;
  font-family: 'times', serif;
  font-size: 1.3em;
  color: $font-color;
  // transition: all .2s ease-out;
  // transition-delay: 1s;
  .underline {
    position: absolute;
    bottom: 5px;
    left: 0;
    width: 100%;
    height: 2px;
    background: rgba($primary, 0.5);
    transform: scale(0,1);
    transform-origin: left;
  }
  &:hover {
    color: $primary;
    .underline {
      transform: scale(1,1);
    }
  }
}
.links {
  // border: 1px solid #111;
  position: relative;
  min-height: 200px;
  &__wrapper {
    display: flex;
    flex-direction: column;
    padding-left: 40px;
    padding-right: 40px;
    &--first {
      // border-right: 1px solid rgba(0, 0, 0, 0.15);
      text-align: right;
      .link_item {
        padding-left: 30px;
        padding-right: 0;
      }
      .link_item .underline {
        transform-origin: right;
      }
    }
  }
}
.divider_vertical {
  position: absolute;
  left: calc(50% - 1px);
  top: 0;
  width: 2px;
  height: 100%;
  background: rgba(0, 0, 0, 0.15);
  transition-delay: .5s;
}
.privacy_policy {
  text-align: right;
  padding-right: 29px;
  letter-spacing: 4px;
}
.social {
  text-align: right;
  padding-right: 29px;
}
.hidden {
  .book__divider {
    transform: scale(0,1);
  }
  .to_top {
    transform: translateY(101%)
  }
  .to_bottom {
    transform: translateY(-101%)
  }
  .line_1 {
    transform: scale(0,1);
  }
  .line_2 {
    transform: scale(1,0);
  }
  .close__text {
    opacity: 0 !important;
    -webkit-transform: translateY(15px);
    transform: translateY(15px);
    pointer-events: none;
  }
  .divider_vertical {
    transform: scale(1,0);
  }
  .link_item {
    transform: translateY(101%);
    // transition-delay: 2s;
  }
  .privacy_policy, .social {
    opacity: 0;
    transform: translateY(15px);
  }
}
@for $i from 1 through 5
  {
    .links__wrapper .overflow-h:nth-child(#{$i}) .link_item {
      transition-delay: .25s * $i;
    }
  }
</style>
