<template>
  <header class="header">
    <div class="menu">
      <div class="bg_vertical black transition_1000_inout" :class="{ 'hide' : !showBg }"></div>
      <div class="menu__button" @click="showMenu">
        <div class="overflow-h">
          <p class="menu__text">MENU</p>
        </div>
        <div class="white_divider"></div>
      </div>
      <div class="logo transition_1000" :class="!showBg ? 'opacity': 'visible'">
        <router-link to="/">
          <img src="@/assets/img/icono.png" alt="Quinta María José">
        </router-link>
      </div>
    </div>

    <router-link to="/" class="book">
      <div class="bg_vertical primary transition_1000_inout"></div>
      <div class="book__button">
        <div class="overflow-h">
          <p class="menu__text hover_black">RESERVA</p>
        </div>
        <div class="white_divider hover_black"></div>
        <div class="overflow-h">
          <p class="menu__text hover_black menu__text--spaced">AHORA</p>
        </div>
      </div>
    </router-link>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      showBg: false,
      breakPoint: 820
    }
  },
  mounted () {
    window.addEventListener('scroll', this.updateScroll)
    this.calcWindowHeight()
    console.log(window.innerHeight)
  },
  methods: {
    showMenu () {
      this.$store.commit('SET_MENU', true)
    },
    updateScroll () {
      if (window.scrollY > this.breakPoint) {
        this.showBg = true
      } else {
        this.showBg = false
      }
    },
    calcWindowHeight () {
      const wh = window.innerHeight
      if (wh < 820) this.breakPoint = wh - 80
    }
  }
}
</script>

<style lang="scss" scoped>
$w1: 50px;
$px1: 22px;
.header {
  position: fixed;
  width: 100%;
  will-change: transform;
  z-index: 100;
  a {
    text-decoration: none;
  }
  .menu {
    position: absolute;
    top: 0;
    left: $px1;
    width: 67px;
    height: 132px;
    // border: 1px solid #FFF;
    pointer-events: all;
    cursor: pointer;
    &__button {
      position: absolute;
      top: 20px;
      left: 8px;
      padding: 10px 0;
      cursor: pointer;
    }
    &__text {
      margin: 0;
      color: #FFF;
      letter-spacing: 3px;
      &--spaced {
        letter-spacing: 6.2px;
      }
    }
  }
  .logo {
    position: absolute;
    top: 75px;
    left: 7px;
    width: $w1;
    &.opacity {
      opacity: 0;
    }
    &.visible {
      transition-delay: 0.25s;
    }
    img {
      width: 100%;
    }
  }
  .white_divider {
    width: $w1;
    height: 5px;
    background: #FFF;
    margin-top: 3px;
    margin-bottom: 3px;
  }
  .book {
    position: absolute;
    top: 0;
    width: 93px;
    // background: $primary;
    height: 95px;
    right: $px1;
    pointer-events: all;
    cursor: pointer;
    &__button {
      position: absolute;
      top: 30px;
      left: 8px;
      cursor: pointer;
    }
    .white_divider {
      width: 77px;
    }
  }
  .bg_vertical {
    width: 100%;
    height: 100%;
    transform-origin: top;
    &.hide {
      transform: scale(1,0)
    }
  }

  .menu:hover, .book:hover {
    .menu__text:not(.hover_black) {
      color: $primary;
    }
    .white_divider:not(.hover_black) {
      background: $primary;
    }
    .menu__text.hover_black {
      color: #f0f0f0;
    }
    .white_divider.hover_black {
      background: #f0f0f0;
    }
  }
}
</style>
