<template>
  <!-- <div class="parallax">
    <img
      ref="img"
      class="w-100"
      :src="image"
    >
  </div> -->
  <!-- <v-parallax :src="image" :height="800"></v-parallax> -->
  <div class="wrapper">
    <img :src="image" alt="" ref="image" :class="imagePosition">
    <div class="content">
      <slot/>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
// import SimpleParallax from 'simple-parallax-js'

export default {
  props: {
    image: {
      type: String,
      default: null
    },
    imagePosition: {
      type: String,
      default: 'center'
    }
  },
  data () {
    return {
      parallaxElement: null,
      config: {
        orientation: 'down',
        scale: 1.4,
        delay: 0.7
      }
    }
  },
  mounted () {
    // this.parallaxElement = new SimpleParallax(this.$refs.img, this.config)
    window.addEventListener('scroll', this.updateScroll)
    // console.log('elem he', this.$el.clientHeight);
    // console.log('position', this.$el.offsetTop)
    // console.log('inner', window.innerHeight)
  },
  methods: {
    updateScroll () {
      const offsetTop = this.$el.offsetTop || 0
      const scrolled = window.pageYOffset - offsetTop,
        rate = scrolled * 0.3
        // if (window.pageYOffset > (offsetTop - window.innerHeight) && this.$refs.image.style){
        //   this.$refs.image.style.transform = `translate3d(0px, ${rate}px, 0px)`
        // }
        if (window.pageYOffset > offsetTop && this.$refs.image.style){
          this.$refs.image.style.transform = `translate3d(0px, ${rate}px, 0px)`
        }
    }
  }
  // destroyed () {
  //   this.parallaxElement.destroy()
  // }
}
</script>

<style lang="scss" scoped>
// .parallax {
//   position: relative;
//   height: 100%;
// }
.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    // object-position: center;
    // transform: scale3d(1.2,1.2,1);
    &.bottom {
      object-position: bottom;
    }
  }
}
</style>
